@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap');

html,
body,
#root,
.app,
.content {
  height: 100%;
  width: 100%;
  /* overflow-x: scroll; */

  font-family: 'Poppins', sans-serif;
}

.app {
  display: flex;
  /* overflow-y: scroll; */

  /* position: relative; */
  position: fixed;

}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #e0e0e0;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

::-webkit-scrollbar-thumb:hover {
  background: #555;
} 
